import React, { useState, useEffect } from "react";
import { Outlet, Link } from "react-router-dom";
import img1 from "../assets/img/imgSalle/La_Salle_pgina_LOGO.png";
const Nav = () => {
  const [scrolled, setScrolled] = useState(false);

  useEffect(() => {
    const handleScroll = () => {
      const isScrolled = window.scrollY > 100;
      setScrolled(isScrolled);
    };

    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <div>
      <div>
        <nav className={scrolled ? "scrolled" : ""}>
          <ul>
            <div className="img-nav">
              <img src={img1} alt="La Salle Logo" />
            </div>
            <li>
              <Link to="/">Home</Link>
            </li>
            <li>
              <Link to="/historia">Historia</Link>
            </li>
            <li>
              <Link to="/la_salle">La Salle</Link>
            </li>
            <li>
              <Link to="/ubicacion">Ubicacion</Link>
            </li>
            <li>
              <Link to="/educacion">Educacion</Link>
            </li>
            <li>
              <Link to="/bolmun">Eventos</Link>
              <ul className="menu-vertical">
                <li>
                  <Link to="/bolmun">Bolmun</Link>
                </li>
                <li>
                  <Link to="/convivencia">Convivencia</Link>
                </li>
                <li>
                  <Link to="/ao_100">100 aos</Link>
                </li>
              </ul>
            </li>
            <li>
              <Link to="/creditos">Creditos</Link>
            </li>
            <li className="login">
              <span className="navbar-text">
                <button
                  className="vvd"
                  onClick={() => console.log("conectado")}
                >
                  <Link className="link-nav" to="/login">
                    <span>Login</span>
                  </Link>
                </button>
              </span>
            </li>
          </ul>
        </nav>
        <Outlet />
      </div>
    </div>
  );
};

export default Nav;
